<template>
    <div class="whatsapp">
        <h3 class="mb-10">Data Pesan</h3>
        <vx-card>
            <vs-table
              :data="sendLog"
               pagination search>
                <template slot="header">
                    <vx-tooltip text="Tambah Pesan" style="display:inline">   
                        <vs-button @click="addMessage = true" class="float-right mb-3" color="#0fabbc" icon="add_circle_outline"> Tambah Pesan</vs-button>
                    </vx-tooltip>
                </template>
                <template slot="thead" >
                    <vs-th sort-key="tglRequest" class="bg-primary text-white text-center">
                    Tanggal Request
                    </vs-th>
                    <vs-th sort-key="tglKirim" class="bg-primary text-white text-center">
                    Tanggal Kirim
                    </vs-th>
                    <vs-th sort-key="isi" class="bg-primary text-white text-center">
                    Pesan
                    </vs-th>
                    <vs-th sort-key="ciuntNumber" class="bg-primary text-white text-center" >
                    Jumlah Number
                    </vs-th>
                    <vs-th class="bg-primary text-white text-center">
                    Aksi
                    </vs-th>
                </template>

                <template slot-scope="{data}">
                    <vs-tr :data="item" :key="index" v-for="(item, index) in data" >
                    <vs-td class="text-center" style="width:10%;">{{item.tglRequest}}</vs-td>
                    <vs-td class="text-center" style="width:10%;">{{item.tglKirim}}</vs-td>
                    <vs-td style="width:55%;">{{item.isi}}</vs-td>
                    <vs-td class="text-center" style="width:10%;">{{item.countNumber}}</vs-td>
                    <vs-td class="text-center" style="width:5%;"><vs-button color="#0fabbc" size="small" icon="pageview" ></vs-button></vs-td>
                    </vs-tr>
                </template>
            </vs-table>
        </vx-card>

        <!-- Add Informasi -->
        <vs-popup class="holamundo"  title="Tambah Pesan" :active.sync="addMessage">
            <CreateMessage />
        </vs-popup>
    </div>
</template>

